<template>
    <el-col :span="20">
        <el-form :model="formData" :rules="rules" ref="formData" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="answerStatus">
                <el-select v-model="formData.answerStatus" placeholder="">
                    <el-option
                            v-for="item in select[0]"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="startTime">
                <el-date-picker
                        v-model="formData.startTime"
                        type="datetime"
                        placeholder=""
                        value-format="yyyy-MM-dd HH:mm:ss">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="endTime">
                <el-date-picker
                        v-model="formData.endTime"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="previewTime">
                <el-date-picker
                        v-model="formData.previewTime"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="answerTime">
                <el-input-number v-model="formData.answerTime" :min="0" :max="10000" label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="questionNumber">
                <el-input-number v-model="formData.questionNumber" :min="0" :max="10000" label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="rewardQuantity">
                <el-input-number v-model="formData.rewardQuantity" :min="0" :max="10000" label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="description">
                <el-input v-model="formData.description" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="rewardType">
                <el-select v-model="formData.rewardType" placeholder="">
                    <el-option
                            v-for="item in select[1]"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="failedShareRate">
                <el-input-number v-model="formData.failedShareRate" :min="0" :max="10000" label=""></el-input-number>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('formData')">{{addTitle}}</el-button>
                <el-button @click="resetForm('formData')"></el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    export default {
        name: 'addSuperActivity',
        mounted() {
            let self = this
            let data = self.$route.query.data;
            if (data) {
                self.addTitle = ''
                self.formData = data;
                self.method = 'PUT'
            }else{
                self.method = 'POST'
            }
        },
        data() {
            return {
                method:'',//
                addTitle: '',
                formData: {
                    answerStatus: '',
                    description: '',
                    answerTime:'',
                    rewardType:'',
                    startTime:'',
                    endTime:'',
                    previewTime:'',
                    questionNumber:'',
                    rewardQuantity:'',
                    failedShareRate:''
                },
                rules: {
                    answerStatus:[{
                        required: true, message: '', trigger: 'change'
                    }],
                    description:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    answerTime:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardType:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    startTime:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    endTime:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    previewTime:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    questionNumber:[{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardQuantity:[{
                        required: true, message: '', trigger: 'change'
                    }],

                },
                select: [
                    [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        },
                        {
                            value: 3,
                            label: ''
                        }
                    ],
                    [
                        {
                            value: 1,
                            label: ''
                        }, {
                            value: 2,
                            label: ''
                        }, {
                            value: 3,
                            label: ''
                        }, {
                            value: 4,
                            label: 'dbex'
                        }
                    ]
                ],
            }
        },
        methods: {
            submitForm(formName) {
                var self = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        self.$ajax({url:'activity/', method: self.method,data:self.formData}).then(function (response) {
                            if (response.code === 1) {
                                self.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success',
                                    duration: 1000
                                });
                            }else{
                                self.$notify({
                                    title: '',
                                    message:response.msg,
                                    type: 'error',
                                    duration:1000
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="stylus">
    .el-rate
        display inline-block
        vertical-align middle

    .el-row
        margin-bottom 20px
</style>





