<template>
    <el-col :span="20">
        <el-form :model="formData" :rules="rules" ref="formData" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="freeProbability">
                <el-input-number v-model="formData.freeProbability"  label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="pointProbability">
                <el-input-number v-model="formData.pointProbability" label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="rewardQuantity">
                <el-input-number v-model="formData.rewardQuantity"  label=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="rewardType">
                <el-select v-model="formData.rewardType"  placeholder="">
                    <el-option
                            v-for="item in options[0]"
                            :key="item.label"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="isImportant">
                <el-select v-model="formData.isImportant"  placeholder="">
                    <el-option
                            v-for="item in options[1]"
                            :key="item.label"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('formData')">{{addTitle}}</el-button>
                <el-button @click="resetForm('formData')"></el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
    export default {
        name:'addTopic',
        mounted(){
            let self = this
            let data = self.$route.query.data;
            if(data){
                self.addTitle = ''
                self.formData = data
                self.method = 'PUT'
            }else{
                self.method = 'POST'
            }
        },
        data() {
            return {
                addTitle:'',
                options:[
                    [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 3,
                            label: ''
                        },
                        {
                            value: 4,
                            label:'DBEX'
                        }
                    ],
                    [
                        {
                            value: 0,
                            label:''
                        },
                        {
                            value: 1,
                            label:''
                        }
                    ]
                ],
                method:'',
                max:4,
                formData: {
                    freeProbability:'',
                    pointProbability:'',
                    rewardQuantity:'',
                    rewardType:'',
                    isImportant:'',
                },
                rules: {
                    freeProbability: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    pointProbability: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardQuantity: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    rewardType: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                    isImportant: [{
                        required: true, message: '', trigger: 'blur'
                    }],
                }
            };
        },
        methods: {
            submitForm(formName) {
                var self = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        self.$ajax({url:'rewardSector/' ,method:self.method ,data:self.formData}).then(function (response) {
                            if(response.code === 1){
                                self.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success',
                                    duration:1000
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="stylus">
    .el-rate
        display inline-block
        vertical-align middle

    .el-row
        margin-bottom 20px
</style>





